import { graphql } from "gatsby"
import * as React from "react"
import { BlogArticle } from "../../components/blog-article"
import SEOHead from "../../components/head"

export default function Insight(props) {
  return <BlogArticle {...props} />
}

export const Head = (props) => {
  const { page } = props.data
  return <SEOHead {...page} />
}

export const query = graphql`
  query ContentfulHomepageFeature($slug: String) {
    contentfulHomepageFeature(slug: { eq: $slug }) {
      body {
        raw
        references {
          ... on ContentfulAsset {
            contentful_id
            __typename
            gatsbyImageData
            alt
            mimeType
            url
          }
        }
      }
      slug
      articleType
      id
      kicker
      heading
      text
      links {
        id
        href
        text
      }
      image {
        id
        gatsbyImageData
        alt
      }
    }
  }
`
