import { GatsbyImage, getImage } from "gatsby-plugin-image"
import * as React from "react"
import Layout from "./layout"
import { Box, Container, Heading, Kicker, Section, Subhead, Text } from "./ui"
import { RichText } from "./rich-text"

export function BlogArticle(props) {
  const { heading, text, image, articleType, body } =
    props?.data?.contentfulHomepageFeature

  return (
    <Layout>
      <Container width="narrow" style={{ marginTop: "40px" }}>
        {image && (
          <GatsbyImage
            alt={image.alt || ""}
            image={getImage(image.gatsbyImageData)}
            style={{ height: "400px", marginBottom: "-50px" }}
          />
        )}
      </Container>
      <Section style={{ background: "white", color: "black" }}>
        <Container width="narrow">
          <Box paddingY={4}>
            <Subhead>
              <Kicker>{articleType}</Kicker>
              {heading}
            </Subhead>
            <Text variant="lead">{text}</Text>
            <Box paddingY={1}>
              <Heading as="h1">{props?.title}</Heading>
              <RichText body={body} />
            </Box>
          </Box>
        </Container>
      </Section>
    </Layout>
  )
}
